@import url(https://fonts.googleapis.com/css2?family=IM+Fell+Double+Pica&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Manrope:wght@600&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Goldman&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

html {
	scroll-behavior: smooth;
}
body {
	font-family: 'Manrope', cursive;
	color: #dceef9;
	background-color: white;
}
.FrontView {
	text-align: center;
	margin: 5px auto;
	height: 100%;
	width: 85%;
	border: 1px solid white;
	box-shadow: 0px 5px 10px #888888;
}

.Socials {
	margin-top: 50px;
	display: flex;
	justify-content: space-around;
	align-items: center;
	background: linear-gradient(45deg, #0c0c0c, hsl(250, 71%, 32%));
	background-size: 400% 400%;
	animation: gradient 10s ease infinite;
}
.TextFooter {
	font-size: 40px;
	color: white;
	font-family: 'Manrope', sans-serif;
}

@keyframes gradient {
	0% {
		background-position: 0% 50%;
	}
	50% {
		background-position: 100% 50%;
	}
	100% {
		background-position: 0% 50%;
	}
}

@media screen and (max-width: 1400px) {
	.Socials {
		display: flex;
		justify-content: center;
		align-items: center;
		flex-direction: column;
	}
}

ul {
	display: flex;
	justify-content: flex-end;

	list-style-type: none;
	font-size: 12px;
}
.sticky {
	position: fixed;
	top: 0;
	width: 100%;
}
.NavContainer {
	font-family: 'Manrope', sans-serif;
	overflow: hidden;

	display: flex;
	justify-content: flex-end;
	background: linear-gradient(45deg, hsl(250, 99%, 60%), #0c0c0c);
	border-bottom: 3px solid grey;
	color: white;
	z-index: 1;
}
li {
	color: white;

	margin: 0px 25px;
	font-size: 25px;
	padding: -5px;
	border-radius: 10px;
}

a {
	text-decoration: none;
}

@media screen and (max-width: 765px) {
	.NavContainer {
		display: flex;
		justify-content: center;
	}
}

.DescriptionCont {
	font-family: 'Goldman', cursive;
	color: white;
	background-color: #8483c3;
	height: 500px;
	display: flex;
	justify-content: center;
	align-items: center;
	flex-direction: column;
	margin-top: 60px;
}

h1 {
	padding-left: 25px;
	text-align: justify;
	color: #41339a;
}
h5 {
	border-top: 3px dashed black;
	text-align: left;
	font-size: 2em;
}
.DescText {
	font-size: 48px;
}
.DescMini {
	display: flex;
	justify-content: flex-start;
	align-items: center;

	text-align: left;
	font-size: 16px;

	margin-left: 10px;
}
.DescMini > div {
	margin-left: 20px;
}
.DescTextMini {
	font-size: 1.25em;
	opacity: 0.90;
	font-family: Goldman;
}
#PersonalPic {
	max-width: 300px;
	max-height: 100px;
	border-radius: 30px;
}

@media screen and (max-width: 765px) {
	.DescMini {
		display: flex;
		justify-content: center;
		align-items: center;
		flex-direction: column-reverse;
	}
	.DescriptionCont {
		height: auto;
	}
}

.Skill {
	font-size: 48px;
	color: #dceef9;
	border: 0.1px solid rgba(240, 161, 161, 0.959);
	background-color: hsl(250, 71%, 60%);
	padding: 25px;
	border-radius: 10px;
	text-align: center;
}
.Skill i {
	transition: all 0.1s ease-in;
	z-index: -1;
}
.Skill i:hover {
	cursor: auto;
	transform: scale(1.5, 1.5);
}

.SkillsContainer {
	display: grid;

	grid-template-columns: 1fr 1fr 1fr;
	grid-row-gap: 25px;
	grid-column-gap: 25px;
	grid-template-rows: auto auto;
}
.LangContainer {
	display: grid;
	grid-template-columns: 1fr 1fr 1fr;
	grid-row-gap: 25px;
	grid-column-gap: 25px;
	grid-template-rows: auto auto;
}
h1 {
	border-bottom: 0.1px solid black;
}
#Skills {
	margin: 5px;
}

@media screen and (max-width: 1400px) {
	.SkillsContainer {
		display: grid;

		grid-template-columns: 1fr;
		grid-row-gap: 25px;
		grid-column-gap: 25px;
		grid-template-rows: 150px;
	}
	.LangContainer {
		display: grid;
		text-align: center;
		grid-template-columns: 1fr;
	}
}

.ContactForm {
	font-size: 32px;
	margin-top: 50px;
	height: 400px;
	color: white;
	background-color: #303030;
}
form {
	display: flex;
	justify-content: center;
	flex-direction: column;
	align-items: center;
}
.Inputs {
	margin: 10px 0;
}
#EmailInput {
	margin-left: 92px;
	width: 250px;
	height: 40px;
}
#MessageInput {
	width: 250px;
	height: 250px;
}

label {
	font-size: 28px;
	margin-right: 50px;
}

#FooterIcons {
	color: white;
	font-size: 48px;
	margin: 20px;
}

#FooterIcons:hover {
	cursor: pointer;
	color: yellow;
	transition: 0.2s ease-in;
}

.Project {
	background-color: hsl(250, 71%, 60%);
	border: 2px solid rgb(202, 189, 189);
	margin: 15px;
	width: auto;
	border-radius: 25px;
	padding: 15px;
	display: flex;
	justify-content: stretch;
	align-items: center;
	flex-direction: column;
}
i {
	color: black;
}
img {
	max-width: 400px;
	max-height: 285px;

	height: auto;
}
.Title {
	font-size: 44px;
	padding-bottom: 5px;
	border-bottom: 1px solid rgb(177, 163, 163);
}
.Description {
	margin-bottom: 10px;
	font-size: 28px;
}
.PreviewSection {
	display: flex;
	justify-content: center;
	align-items: stretch;
}
.GitHubMess:hover {
	border-top: 0.1px solid white;
	border-bottom: 0.1px solid white;
	border-radius: 1px;
	cursor: pointer;
}
.GitHubMess {
	display: flex;
	justify-content: center;
	align-items: center;
	margin: 5px;
}

.LeaveSite {
	display: flex;
	justify-content: center;
	align-items: center;
	margin: 5px;
}
.LeaveSite:hover {
	border-top: 0.1px solid white;
	border-bottom: 0.1px solid white;
	border-radius: 1px;
	cursor: pointer;
}
a {
	color: inherit;
	text-decoration: none;
}

i {
	color: black;
	font-size: 48px;
	margin: 20px;
}

i:hover {
	cursor: pointer;
	color: yellow;
	transition: 0.2s ease-in;
}

@media screen and (max-width: 690px) {
	img {
		max-width: 200px;
		max-height: 300px;

		height: auto;
	}
}

.Projects {
	margin-top: 25px;
	display: grid;
	grid-template-columns: 50% 50%;
}
#MyProj {
	margin-top: 50px;
}

@media screen and (max-width: 1400px) {
	.Projects {
		margin-top: 25px;
		display: flex;
		flex-direction: column;
		justify-content: center;
		align-items: center;
	}
}

