.Projects {
	margin-top: 25px;
	display: grid;
	grid-template-columns: 50% 50%;
}
#MyProj {
	margin-top: 50px;
}

@media screen and (max-width: 1400px) {
	.Projects {
		margin-top: 25px;
		display: flex;
		flex-direction: column;
		justify-content: center;
		align-items: center;
	}
}
